import React, {Component} from 'react'
import { List, Datagrid, TextField, EmailField, UrlField, Edit, SimpleForm, TextInput, DisabledInput, ReferenceInput, SelectInput, Responsive, SimpleList } from 'react-admin';
import MyUrlField from './myUrlField';

export const Users = props => (
    <List {...props}>
        <Responsive
        small={
            <SimpleList rowClick="edit"
                primaryText={user => user.username}
                secondaryText={user => `id ${user.id}`}
            />
        }
        medium={
            <Datagrid rowClick="edit">
            <TextField source="id" />
            <TextField source="name" />
            <TextField source="username" />
            <EmailField source="email" />
            <TextField source="address.street" />
            <TextField source="phone" />
            <MyUrlField source="website" />
            <TextField source="company.name" />
            </Datagrid>
        }
        />
    </List>
);

export const MyEdit = props => (
    <Edit {...props}>
        <SimpleForm>
            <DisabledInput source="id" />
            <ReferenceInput source="userId" reference="users">
                <SelectInput optionText="name" />
            </ReferenceInput>
            <TextInput source="name" />
            <TextInput source="username" />
            <TextInput source="email" />
            <TextInput source="address.street" />
            <TextInput source="phone" />
            <TextInput source="website" />
            <TextInput source="company.name" />
        </SimpleForm>
    </Edit>
);