import * as Yup from "yup";

const phoneRegExp = /^((8|\+7)[\- ]?)?(\(?\d{3}\)?[\- ]?)?[\d\- ]{7,10}$/
const available = /^([а-яё]+|[a-z]+)$/i

const RegisterForm = Yup.object().shape({
  firstname: Yup.string()
  .matches(available, 'Вы можете использовать только буквы А-я или A-z')
  .min(2, 'Длинна не может быть меньше 2 символов')
  .max(30, 'Длинна не может привышать 30 символов')
  .required('Это поле должно быть заполнено'),
  lastname: Yup.string()
  .matches(available, 'Вы можете использовать только буквы А-я или A-z')
  .min(2, 'Длинна не может быть меньше 2 символов')
  .max(30, 'Длинна не может привышать 30 символов')
  .required('Это поле должно быть заполнено'),
  patronymic: Yup.string()
  .matches(available, 'Вы можете использовать только буквы А-я или A-z')
  .min(2, 'Длинна не может быть меньше 2 символов')
  .max(30, 'Длинна не может привышать 30 символов')
  .required('Это поле должно быть заполнено'),
  email: Yup.string()
    .email("Неправильный e-mail адрес")
    //не сабмитим, если поле не заполнено
    .required("Это поле должно быть заполнено"),
  password: Yup.string()
    .min(8, "Пароль должен состоять минимум из 8 символов")
    .required("Это поле должно быть заполнено"),
  re_password: Yup.string()
    .oneOf([Yup.ref('password'), null], "Пароли не совпадают")
    .required('Это поле должно быть заполнено'),
  phone: Yup.string().matches(phoneRegExp, 'Неверный формат телефона'),
  code: Yup.string()
  .required()
});
export default RegisterForm;