import React, { Fragment } from 'react';
import { withRouter, Redirect } from 'react-router-dom';
import { Row, Col, Container, Form, Button } from 'react-bootstrap';
import { withCookies } from 'react-cookie';
import './style.scss';

class Role extends React.Component {
    // Tutorial: https://medium.freecodecamp.org/login-using-react-redux-redux-saga-86b26c8180e
    constructor(props) {
    super(props);
    this.state = {
        redirect: false,
        value: 'Выберите',
    };
    }


    render() {

        if(this.state.redirect) { //isAuthenticated) {
            return <Redirect to="/login" />;
        } else {
            return (
                <Fragment>
                    <div className="invitation-block">
                        <div className="invitation-label">
                            <span>Есть приглашение от собственника?</span>
                        </div>
                        <p>Пожалуйста, укажите ваш номер телефона и код, который вы получили от собственника недвижимости.</p>
                        <Form>
                        <Form.Group>
                            <Form.Control placeholder="Введите ваш номер телефона"></Form.Control>
                        </Form.Group>

                        <Form.Group>
                            <Form.Control placeholder="Код от собственника"></Form.Control>
                        </Form.Group>
                        <Form.Group>
                        <Button variant="primary" type="submit">
                            Зарегестрироваться
                        </Button>
                        </Form.Group>
                    </Form>
                    </div>
                </Fragment>
            )
        }

    }
}

export default withCookies(withRouter(Role));