import React, { Fragment } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import ScrollReveal from 'scrollreveal';

class Using extends React.Component {

    componentDidMount() {
        ScrollReveal().reveal('.features-item', { scale: 0.85 });
    }

    render() {

        return (
            <Fragment>
                    <Container className="using-container">
                        <Row>
                            <Col xs={12}>
                                <h2>САМЫЙ ЛЕГКИЙ ПУТЬ К СОТРУДНИЧЕСТВУ</h2>
                                <p>мы не усложняем, а упрощаем оказание услуг</p>
                            </Col>
                            <Col className="using-item" xs={12} md={4}><div><i class="fas fa-clipboard-list"></i></div>Вы выбираете услуги</Col>
                            <Col className="using-item" xs={12} md={4}><div><i class="fas fa-pencil-alt"></i></div>Заключаем договор</Col>
                            <Col className="using-item" xs={12} md={4}><div><i class="fas fa-rocket"></i></div>Выполняем услуги</Col>
                        </Row>
                    </Container>
            </Fragment>
        )
    }
}

export default Using;