import React, { Fragment } from 'react';
import { withRouter, Redirect } from 'react-router-dom';
import { observer } from 'mobx-react';
import { Row, Col, Button, Container } from 'react-bootstrap';
import { Formik, Field } from "formik";
import { withCookies } from 'react-cookie';
import Form from 'react-bootstrap/Form';
import './style.scss';
import registerForm from '../../../schemes/registerForm';

class Registration extends React.Component {
    // Tutorial: https://medium.freecodecamp.org/login-using-react-redux-redux-saga-86b26c8180e

    state = {
        redirect: false,
        firstname: '',
        lastname: '',
        email: '',
        date: '',
        password: '',
        re_password: '',
        errors: []
    };


    componentDidMount() {
    }

    async componentWillReact() {
        if(this.props.store.isAuthenticated) {
            // await this.props.store.fetchProfile()
            this.props.history.push('/')
        }
    }

    changeValue(e) {
        this.setState({ [e.target.name]: e.target.value });
    }

    onHandleRegister = (event) => {
        event.preventDefault();
        if(this.state.email === 'admin' && this.state.password === 'admin') {
            this.props.cookies.set('auth', true);
            this.props.store.login();
            this.setState({ redirect: true });
        }
      //  this.props.store.login({ email: this.state.email, password: this.state.password });
    }

    render() {

        if(this.state.redirect) { //isAuthenticated) {
            return <Redirect to="/login" />;
        }
        else {
            return (
                <Fragment>
                    <Container className="register-container">
                        <Row>
                            <Col className="register-block">
                                { this.state.errors.length > 0 ?
                                    <div className="errors-block">
                                        <h4>Ошибки</h4>
                                        <ul>
                                            <li>Ошибки</li>
                                        </ul>
                                    </div>
                                    : null
                                    }
                                    <Formik
                                    //инициализируем значения input-ов
                                    initialValues={{
                                        email: "",
                                        username: "",
                                        phone: "",
                                        password: "",
                                        re_password: "",
                                        lastname: "",
                                        patronymic: "",
                                        code: "",
                                    }}
                                    //подключаем схему валидации, которую описали выше
                                    validationSchema={registerForm}
                                    //определяем, что будет происходить при вызове onsubmit
                                    onSubmit={values => {
                                        setTimeout(() => {
                                        alert(JSON.stringify(values, null, 2));
                                        }, 500);
                                    }}
                                    //свойство, где описывыем нашу форму
                                    //errors-ошибки валидации формы
                                    //touched-поля формы, которые мы "затронули",
                                    //то есть, в которых что-то ввели
                                    render={({ errors, touched }) => (
                                        <Form className="form-container">

                                    <Form.Group>
                                        <Form.Label>Имя<span className="required">*</span></Form.Label>
                                        <Field name="firstname" className="form-control" type="text" placeholder="Иван" />
                                        { errors.firstname && touched.firstname ? (<div className="errors">{errors.firstname}</div>) : null}
                                    </Form.Group> 

                                    <Form.Group>
                                        <Form.Label>Фамилия<span className="required">*</span></Form.Label>
                                        <Field name="lastname" className="form-control" type="text" placeholder="Иванов" />
                                        { errors.lastname && touched.lastname ? (<div className="errors">{errors.lastname}</div>) : null}
                                    </Form.Group> 

                                    <Form.Group>
                                        <Form.Label>Отчество(Если есть)</Form.Label>
                                        <Field name="patronymic" className="form-control" type="text" placeholder="Иван" />
                                        { errors.patronymic && touched.patronymic ? (<div className="errors">{errors.patronymic}</div>) : null}
                                    </Form.Group> 

                                    <Form.Group>
                                        <Form.Label>Электронный адрес<span className="required">*</span></Form.Label>
                                        <Field name="email" className="form-control" type="email" placeholder="example@mail.ru" />
                                        { errors.email && touched.email ? (<div className="errors">{errors.email}</div>) : null}
                                    </Form.Group>   

                                    <Form.Group>
                                        <Form.Label>Мобильный телефон</Form.Label>
                                        <Field name="phone" className="form-control" type="phone" placeholder="+7 000 000 00 00" />
                                        { errors.phone && touched.phone ? (<div className="errors">{errors.phone}</div>) : null}
                                    </Form.Group>   

                                    <Form.Group>
                                        <Form.Label>Пароль<span className="required">*</span></Form.Label>
                                        <Field name="password" className="form-control" type="password" placeholder="Пароль" />
                                        { errors.password && touched.password ? (<div className="errors">{errors.password}</div>) : null}
                                    </Form.Group> 

                                    <Form.Group>
                                        <Form.Label>Повторите пароль<span className="required">*</span></Form.Label>
                                        <Field name="re_password" className="form-control" type="password" placeholder="Повторите пароль" />
                                        { errors.re_password && touched.re_password ? (<div className="errors">{errors.re_password}</div>) : null}
                                    </Form.Group>
                                    { this.props.role === 'Арендатор' ?
                                    <Form.Group>
                                        <Form.Label>Код приглашения от собственника<span className="required">*</span></Form.Label>
                                        <Field name="code" className="form-control" type="text" placeholder="Код доступа" />
                                        { errors.code && touched.code ? (<div className="errors">{errors.code}</div>) : null}
                                    </Form.Group>  
                                    : null
                                    }

                                    { this.props.role === 'ТСЖ' || this.props.role === 'Председатель совета' || this.props.role === 'УК' ? 
                                    <Form.Group>
                                    <Form.Group>
                                        <Form.Label>Название компании<span className="required">*</span></Form.Label>
                                        <Field name="code" className="form-control" type="text" placeholder="Название" />
                                        { errors.code && touched.code ? (<div className="errors">{errors.code}</div>) : null}
                                    </Form.Group>
                                    <Form.Group>
                                        <Form.Label>Роль в компании<span className="required">*</span></Form.Label>
                                        <Field name="code" className="form-control" type="text" placeholder="Роль" />
                                        { errors.code && touched.code ? (<div className="errors">{errors.code}</div>) : null}
                                    </Form.Group> 
                                    </Form.Group>  
                                    : null
                                    }
                                    <Form.Group id="formGridCheckbox">
                                        <Form.Check>
                                            <Form.Check.Input type='checkbox' />
                                            <Form.Check.Label>Я согласен с <a href="/rules">правилами</a> нашего сайта.</Form.Check.Label>
                                        </Form.Check>
                                    </Form.Group>
                                    <Form.Group>
                                        <Button>Зарегестрироваться</Button>
                                    </Form.Group>
                                    </Form>
                                    )}
                                />
                            </Col>
                        </Row>
                    </Container>
                </Fragment>
            )
        }

    }
}

export default withCookies(withRouter(Registration));