import React, { Fragment } from 'react';
import { Redirect } from 'react-router-dom';
import './style.scss';  
import { Container, Row } from 'react-bootstrap';
import About from './about';
import Choice from './choice';
import ScrollReveal from 'scrollreveal';
import Using from './using';
import Scene from '../../threejs/scene';
import { Steps, Hints } from 'intro.js-react';
import 'intro.js/introjs.css';

class MainPage extends React.Component {
    // Главная страница

    constructor(props) {
        super(props);
        this.state = { 
            navigate: false,
            stepsEnabled: false,
            initialStep: 0,
            steps: [
              {
                element: '#text',
                intro: 'Main step',
              },
              {
                element: '#btn',
                intro: 'Попробовать',
              },
            ],
         }
    }

    onExit = () => {
        this.setState(() => ({ stepsEnabled: false }));
    };

    onStart = () => {
        this.setState(() => ({ stepsEnabled: true }));
    }

    componentDidMount() {
        ScrollReveal().reveal('.container-wrapper', { scale: 0.8 });
        ScrollReveal().reveal('.features-item');
        ScrollReveal().sync();
        // const userId = 1;
        // this.props.store.getProfile(userId);
    }

    redirectTo() {
        this.setState({ navigate: true });
    }

    render() {

        const { stepsEnabled, steps, initialStep, hintsEnabled, hints } = this.state;

        return (
            <Fragment>
                <Container fluid className="container-wrapper">
                    <Row>
                    <Container fluid className="main-container">
                        <Row>
                            <Container>
                                <Row>
                                <Steps
                                enabled={stepsEnabled}
                                steps={steps}
                                initialStep={initialStep}
                                onExit={this.onExit}
                                />
                                    <div className="main-banner" id="main">
                                        <h1 id="text">Лучший сервис по обслуживанию жилых домов и комплексов</h1>
                                        <button id="btn" className="btn-s btn-action btn_lg btn-green" onClick={() => this.onStart()}>Попробовать</button>
                                    </div>
                                </Row>
                            </Container>
                        </Row>
                    </Container>
                    <Container>
                        <Scene />
                    </Container>
                    <Choice />
                    <About />
                    <Using />
                    </Row>
                </Container>
            </Fragment>
        )
    }
}

export default MainPage;