import { fetchUtils, Admin, Resource, ListGuesser, EditGuesser } from 'react-admin';
import simpleRestProvider from 'ra-data-simple-rest';
import jsonServerProvider from 'ra-data-json-server';
import React, {Component} from 'react'
import Menu from './menu'; 
import authProvider from './AuthProvide/auth';
import { Users, MyEdit } from './Users/index';

class index extends Component {

    render() {
        const httpClient = (url, options = {}) => {
            if (!options.headers) {
                options.headers = new Headers({ Accept: 'application/json' });
            }
            const token = localStorage.getItem('token');
            options.headers.set('Authorization', `Bearer ${token}`);
            return fetchUtils.fetchJson(url, options);
        }
        const dataProvider = jsonServerProvider('https://jsonplaceholder.typicode.com', httpClient);

        return (
            <Admin authProvider={authProvider} dataProvider={dataProvider}>
                <Resource name="users" list={Users} edit={MyEdit} />
            </Admin>
        )
    }
}

export default index;