import React, { Fragment } from 'react';
import { withRouter, Redirect } from 'react-router-dom';
import { observer } from 'mobx-react';
import { Row, Col, Button, Container } from 'react-bootstrap';
import { withCookies } from 'react-cookie';
import './style.scss';
import Role from './role';
import 'rc-steps/assets/index.css';
import 'rc-steps/assets/iconfont.css';
import Steps, { Step } from 'rc-steps';
import RoleAction from './roleAction';
import invitation from './invitation';
import Apartment from './apartment';

class Register extends React.Component {
    // Tutorial: https://medium.freecodecamp.org/login-using-react-redux-redux-saga-86b26c8180e

    constructor(props) {
        super(props);
        this.state = {
            redirect: false,
            current: 1,
            role: ''
        };
        this.setRole = this.setRole.bind(this);
    }


    componentDidMount() {

    }

    async componentWillReact() {
        if(this.props.isAuthenticated) {
            // await this.props.store.fetchProfile()
            this.props.history.push('/')
        }
    }

    setRole(arg) {
        this.setState({ role: arg });
    }

    render() {

        const screens = [
            { text: '1', component: Role  },
            { text: '3', component: RoleAction }
        ]

        const StepsScreens = () => { 
            let ScreenNow = screens[this.state.current - 1].component;   
                return <ScreenNow role={this.state.role} setRole={this.setRole} />
         }

        const Buttons = () => (
            
            <div className="buttons">
                { this.state.current > 1 ? 
                <div className="left-button">
                    <button className="btn-s btn_md btn-action btn-green" onClick={() => this.setState({ current: this.state.current > 0 ? this.state.current - 1 : this.state.current })}>
                        Назад
                    </button>
                </div> : null }
                <div className="right-button">
                    <button disabled={this.state.role !== '' ? false : true} className="btn-s btn_md btn-action btn-green" onClick={() => this.setState({ current: this.state.current < screens.length ? this.state.current + 1 : this.state.current })}>
                        Далее
                    </button>
                </div>    
            </div>
        )

        if(this.state.redirect) { //isAuthenticated) {
            return <Redirect to="/login" />;
        }
        else {
            return (
                <Fragment>
                    <Container className="flex-1">
                        <Row className="registration-wrapper">
                            <Col xs={12} className="registration-steps">
                                <Steps labelPlacement="vertical" current={this.state.current - 1}>
                                    <Step title="Роль"></Step>
                                    <Step title="Регистрация"></Step>
                                </Steps>
                                <StepsScreens />
                                { this.state.current < screens.length ?
                                <Buttons />
                                : null
                                }
                            </Col>
                        </Row>
                    </Container>
                </Fragment>
            )
        }

    }
}

Register = observer(Register);

export default withCookies(withRouter(Register));