import React from 'react';
import { observer } from 'mobx-react';
import './style.scss';
import { Link } from 'react-router-dom';
import { Nav, Navbar, NavDropdown } from 'react-bootstrap';
import { withCookies } from 'react-cookie';
import { connect } from 'react-redux';
import { setUser, setAuth, removeUser } from '../../../actions';
import { bindActionCreators } from 'redux';
import tokenVerify from '../../tokenVerify.jsx';
import { checkSession } from '../../../CitizenAPI';

class Header extends React.Component {
    // Шапка сайта

    componentDidMount() {
        // const userId = 1;
        if(this.props.cookies.get('auth') !== undefined) {
            checkSession({ session: this.props.cookies.get('auth') }).then((data) => {
                if(JSON.parse(data).result === true) {
                    this.props.setAuth(true);
                }
            })
        /* this.props.tokenVerify(this.props.cookies.get('auth'))
            .then((data) => this.props.setAuth(data)) */
        } 
        // this.props.store.getProfile(userId);
    }


    async onEventLogout() {
        this.props.setAuth(false);
        this.props.setUser({});
        this.props.cookies.remove('auth');
    }

    render() {
      //  const { citizen, isLoading } = this.props.store;
     //   const { isAuthenticated } = this.props.store;

        return (
                    <Navbar className="app-header" expand="lg">
                        <Navbar.Brand className="mr-auto" href="#home"><img alt="logo"  src={require('../../../images/Ural_yoldyzy_1.png')} ></img></Navbar.Brand>
                        <Navbar.Toggle><i className="fas fa-bars fx-2"></i></Navbar.Toggle>
                        <Navbar.Collapse id="basic-navbar-nav navbar-inverse">
                            <Nav className="mr-auto navbar-links">
                            <Nav.Item>
                                <Link to="/">Главная</Link>
                            </Nav.Item>
                                <Nav.Item>
                                <Link to="/mce">ЖКХ</Link>
                                </Nav.Item>
                                <Nav.Item>
                                <Link to="/updates">Объявления</Link>
                                </Nav.Item>
                                <Nav.Item>
                                <Link to="/votes">Голосование</Link>
                                </Nav.Item>   
                                <Nav.Item>
                                <Link to="/contacts">Контакты</Link>
                                </Nav.Item>
                            </Nav>
                            {  this.props.auth === true ? 
                                <Nav>
                                    <NavDropdown title="Аккаунт" id="basic-nav-dropdown">
                                        <Link to="/profile">Профиль</Link>
                                        <NavDropdown.Divider />
                                        <Link to="#" onClick={() => this.onEventLogout()}>Выйти</Link>
                                    </NavDropdown>
                                </Nav>
                                :
                                <Nav>
                                    <Nav.Item>
                                        <Link to="/login">Войти</Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Link to="/register">Регистрация</Link>
                                    </Nav.Item>
                                </Nav>
                            }
                        </Navbar.Collapse>
                    </Navbar>
        )
    }
}

function mapStateToProps(state, ownProps) {
    return {
      user: state.user,
      auth: state.auth,
      cookies: ownProps.cookies
    };
}
  
const mapDispatchToProps = (dispatch) => bindActionCreators({ setUser, setAuth, removeUser }, dispatch);

export default withCookies(connect(mapStateToProps, mapDispatchToProps)(Header));