import React, { Fragment } from 'react';
import { withRouter, Redirect } from 'react-router-dom';
import { Row, Col, Container } from 'react-bootstrap';
import { withCookies } from 'react-cookie';
import './style.scss';
import Invitation from './invitation';
import Registration from './register';
import CardCompany from './cardCompany';

class Role extends React.Component {
    // Tutorial: https://medium.freecodecamp.org/login-using-react-redux-redux-saga-86b26c8180e
    constructor(props) {
    super(props);
    this.state = {
        redirect: false,
        current: 0
    };
    }

    switcher() { 
        switch(this.props.role) {
            case "Сожитель": return 0;
            case "Собственник": return 0;
            case "Арендатор": return 0;
            case "ТСЖ": return 0;
            case "Председатель совета": return 0;
            case "УК": return 0;
            default: return 0;
        }
     }

    componentWillMount() {
        this.setState({ current: this.switcher() });
    }


    render() {

        const screens = [
            { text: 1, component: Registration  },
            { text: 3, component: CardCompany }
        ]

        const StepsScreens = () => { 
            let ScreenNow = screens[this.state.current].component;   
                return <ScreenNow role={this.props.role} />
         }

        if(this.state.redirect) { //isAuthenticated) {
            return <Redirect to="/login" />;
        } else {
            return (
                <Fragment>
                    <StepsScreens />
                </Fragment>
            )
        }

    }
}

export default withCookies(withRouter(Role));