import React, {Component} from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import { Table } from 'semantic-ui-react'

class index extends Component {
    render() {
        return (
            <div className={this.props.show === true ? "votes-people show" : "votes-people"}>
                <div className="votes-people-bg" onClick={() => this.props.changeShow()}></div>
                <div className="votes-people-group-block">
                        <p>Голоса</p>
                        <div className="divider"></div>
                        <div className="votes-people-group">
                        <Table unstackable>
                            <Table.Header>
                            <Table.Row>
                                <Table.HeaderCell>Собственник</Table.HeaderCell>
                                <Table.HeaderCell>Статус</Table.HeaderCell>
                            </Table.Row>
                            </Table.Header>
                            <Table.Body>
                            { this.props.votes.map((item) => (
                                <Table.Row>
                                    <Table.Cell>{ item.initials }</Table.Cell>
                                    <Table.Cell>{ 
                                        item.vote === 'Да' 
                                        ? <span className="green">Согласен</span> 
                                        : item.vote === 'Нет' 
                                        ? <span className="red">Не согласен</span> 
                                        : <span className="green">Воздержался</span>}</Table.Cell>
                                </Table.Row>)) 
                            }
                            </Table.Body>
                        </Table>
                        </div>
                </div>
            </div>
        )
    }
}

export default index;