import React from "react";

import * as THREE from 'three';

class DemoScene extends React.Component {

  componentDidMount() {
    const width = this.mount.clientWidth
    const height = this.mount.clientHeight
    this.scene = new THREE.Scene();
    this.camera = new THREE.PerspectiveCamera(
      75,
      width / height,
      0.1,
      1000
    )
    this.camera.position.set(0,0,10);
    this.renderer = new THREE.WebGLRenderer({ antialias: true })
    this.renderer.setClearColor('#000000')
    this.renderer.setSize(width, height)
    this.mount.appendChild(this.renderer.domElement)    //ADD CUBE
    let light = new THREE.AmbientLight( 0x888888 );
    this.scene.add( light );

    light = new THREE.DirectionalLight( 0xfdfcf0, 1 )
    light.position.set(20,10,20)
    this.scene.add( light )

    let earthGeometry = new THREE.SphereGeometry( 5, 50, 50 );
    let earthMaterial = new THREE.MeshPhongMaterial({
      color: 'white',
      shininess: 25,
    });

    this.earth = new THREE.Mesh(earthGeometry, earthMaterial);
    
    //add the earth mesh to the scene
    this.scene.add(this.earth);

    this.start();
  }

    start = () => {
      if (!this.frameId) {
        this.frameId = requestAnimationFrame(this.animate)
      }
    }
    stop = () => {
      cancelAnimationFrame(this.frameId)
    }
    animate = () => {
    this.earth.rotation.x += 0.01
    this.earth.rotation.y += 0.01   
    this.renderScene()
    this.frameId = window.requestAnimationFrame(this.animate)
  }
    renderScene = () => {
      this.renderer.render(this.scene, this.camera)
    }

  render() {

    return (
      <div
      style={{ width: '400px', height: '400px' }}
      ref={(mount) => { this.mount = mount }}
    />
    );
  }
}

export default DemoScene;