import React from 'react';
import { BrowserRouter as Router, Switch, Route, Redirect, BrowserRouter } from "react-router-dom";
import './App.css';
import MeterMeasurements from './components/Blocks/MeterMeasurements/index';
import Payment from './components/Blocks/Payment/index';
import Login from './components/Blocks/Login/index';
import Header from './components/Layouts/Header/index';
import Footer from './components/Layouts/Footer/index';
import Profile from './components/Blocks/Profile/index';
import MainPage from './components/Blocks/MainPage/index';
import 'semantic-ui-css/semantic.min.css'
import Register from './components/Blocks/Register/index';
import Votes from './components/Blocks/Votes/index';
import Admin from './admin/Admin/index';
import { withCookies } from 'react-cookie';
import Layout from './components/Layouts/layout';
import adminLayout from './components/Layouts/admin/layout';
import { CookiesProvider } from 'react-cookie';

class App extends React.Component {

  render() {

    const AppRoute = ({ component: Component, layout: Layout, ...rest }) => (
      <Route {...rest} render={props => (
        <Layout>
          <Component {...props} />
        </Layout>
      )} />
    )
    
    const PrivateRoute = ({ component: Component, layout: Layout, ...rest }) => (
      <Route {...rest} render={(props) => (
        this.props.cookies.get('auth') !== undefined
          ? <Layout><Component {...props} /></Layout>
          : <Redirect to='/login' />
      )} />
    )

    return (
        <BrowserRouter>
        <CookiesProvider>
          <Router basename={'/'}>
                    <AppRoute path="/login" layout={Layout} component={() => <Login />} />
                    <AppRoute path="/register" layout={Layout} component={() => <Register />} />
                    <AppRoute exact path="/" layout={Layout} component={() => <MainPage />} />
                    <PrivateRoute path="/profile" layout={Layout} component={() => <Profile />} />
                    <PrivateRoute path="/ad" layout={Layout} component={() => <Profile />} />
                    <PrivateRoute path="/votes" layout={Layout} component={() => <Votes />} />
                    <PrivateRoute path="/mce" layout={Layout} component={() => <MeterMeasurements />} />
                    <PrivateRoute path="/payment" layout={Layout} component={() => <Payment />} /> 
                    <PrivateRoute path="/admin" layout={adminLayout} component={() => <Admin />} />          
          </Router> 
        </CookiesProvider>
        </BrowserRouter>
    );
  }
}

export default withCookies(App);
