import React, { Fragment } from 'react';
import { withRouter, Redirect } from 'react-router-dom';
import { observer } from 'mobx-react';
import { Row, Col, Container } from 'react-bootstrap';
import { withCookies } from 'react-cookie';
// import Cookies from 'universal-cookie';
import { connect } from 'react-redux';
import { setUser, setAuth, removeUser } from '../../../actions';
import { bindActionCreators } from 'redux';
import Form from 'react-bootstrap/Form';
import { login } from '../../../CitizenAPI';
import './style.scss';

// const cookies = new Cookies();

class Login extends React.Component {
    // Tutorial: https://medium.freecodecamp.org/login-using-react-redux-redux-saga-86b26c8180e

    state = {
        redirect: false,
        email: '',
        password: '',
        errors: []
    };

    async componentWillReact() {
        if(this.props.auth === true) {
            // await this.props.store.fetchProfile()
            this.props.history.push('/')
        }
    }

    changeValue(e) {
        this.setState({ [e.target.name]: e.target.value });
    }

    onHandleLogin = async(event) => {
        event.preventDefault();
        //    this.props.cookies.set('auth', true);
        await login({ user: this.state.email, password: this.state.password })
            .then(async(data) => { 
                data = JSON.parse(data);
                if(data.result !== undefined)  {
                   await this.props.setAuth(true);
                   await this.props.cookies.set('auth', data.result, { expires: new Date(Date.now() + (1000 * 60 * 60 * 24 * 7)) });
                   await this.setState({ redirect: true });
                }
                if(data.error !== undefined) {
                    this.setState({ errors: 'Неправильный логин или пароль' });
                }
            })
            .catch(data => {
                this.setState({ errors: data });
            })
    }

    render() {

        if(this.state.redirect === true) { //isAuthenticated) {
            return <Redirect to="/" />;
        }
        else {
            return (
                <Fragment>
                    <Container className="login-container">
                        <Row>
                            <Col className="login-block">
                                <h1>ВХОД</h1>
                                { this.state.errors ? <p>{ this.state.errors }</p> : null }
                                <Form>
                                    <Form.Group controlId="formBasicEmail">
                                        <Form.Label>E-mail</Form.Label>
                                        <Form.Control name="email" type="email" onChange={(e) => this.changeValue(e)} value={this.state.email} placeholder="Введите E-mail" />
                                    </Form.Group>
                                    <Form.Group controlId="formBasicPassword">
                                        <Form.Label>Пароль</Form.Label>
                                        <Form.Control name="password" type="password" onChange={(e) => this.changeValue(e)} value={this.state.password} placeholder="Введите пароль" />
                                    </Form.Group>
                                    <button className="btn-s btn_md btn-action btn-green" variant="primary" type="submit" onClick={this.onHandleLogin}>
                                        Войти
                                    </button>
                                </Form>
                            </Col>
                        </Row>
                    </Container>
                </Fragment>
            )
        }

    }
}

function mapStateToProps(state, ownProps) {
    return {
      user: state.user,
      auth: state.auth,
      cookies: ownProps.cookies
    };
}
  
const mapDispatchToProps = (dispatch) => bindActionCreators({ setUser, setAuth, removeUser }, dispatch);

export default withCookies(withRouter(connect(mapStateToProps, mapDispatchToProps)(Login)));