import React, { Fragment } from 'react';
// import { observer } from 'mobx-react';
import { Col, Container, Row, Image } from 'react-bootstrap';
import { Divider, Grid, Segment, Table } from 'semantic-ui-react';
import './style.scss';

class Payment extends React.Component {
    // Страница формы оплаты ЖКХ

    constructor(props) {
        super(props);
        this.state = { 
            firstname: 'Иван',
            lastname: 'Иванов',
            patronymic: 'Иванович',
            email: 'ivanivanov@gmail.com',
            role: 'Собственник',
            city: 'Екатеринбург',
            building: 'Жилой дом',
            entrance: 2,
            stage: 3,
            managementCompany: 'Упр. компания',
            street: 'Светланская',
            house: 123,
            housing: 1,
            apartment: 20
         };
    }

    componentDidMount() {
    }

    render() {
        return (
            <Fragment>
                <Container fluid className="profile-container">
                    <Row>
                        <Col xs={12} className="profile-background"></Col>
                        <Col xs={12}>
                            <div className="profile-image-support">
                                <div className="profile-image">
                                    <div>
                                        <Image src={require('../../../images/avatar.png')} fluid />
                                    </div>
                                    <p>{this.state.firstname} {this.state.lastname}</p>
                                </div>
                            </div>
                        </Col>
                        <Container>
                            <Row>
                                <Col className="profile-info-block" md={12} xs={12}>
                                            <Divider horizontal>Основная информация</Divider>
                                            <div className="profile-info-item">
                                                <div className="profile-info-item-label">Имя</div>
                                                <div className="profile-info-item-text">{ this.state.firstname }</div>
                                            </div>
                                            <div className="profile-info-item">
                                                <div className="profile-info-item-label">Фамилия</div>
                                                <div className="profile-info-item-text">{ this.state.lastname }</div>
                                            </div>
                                            <div className="profile-info-item">
                                                <div className="profile-info-item-label">E-mail</div>
                                                <div className="profile-info-item-text">{ this.state.email }</div>
                                            </div>
                                            <div className="profile-info-item">
                                                <div className="profile-info-item-label">Роль</div>
                                                <div className="profile-info-item-text">{ this.state.role }</div>
                                            </div>
                                            <Divider horizontal>Здание</Divider>
                                            <div className="profile-info-item">
                                                <div className="profile-info-item-label">Город</div>
                                                <div className="profile-info-item-text">{ this.state.city }</div>
                                            </div>
                                            <div className="profile-info-item">
                                                <div className="profile-info-item-label">Улица</div>
                                                <div className="profile-info-item-text">{ this.state.street }</div>
                                            </div>
                                            <div className="profile-info-item">
                                                <div className="profile-info-item-label">Дом</div>
                                                <div className="profile-info-item-text">{ this.state.house }</div>
                                            </div>
                                            <div className="profile-info-item">
                                                <div className="profile-info-item-label">Корпус</div>
                                                <div className="profile-info-item-text">{ this.state.housing }</div>
                                            </div>
                                            <div className="profile-info-item">
                                                <div className="profile-info-item-label">Квартира</div>
                                                <div className="profile-info-item-text">{ this.state.apartment }</div>
                                            </div>
                                            <div className="profile-info-item">
                                                <div className="profile-info-item-label">Тип задния</div>
                                                <div className="profile-info-item-text">{ this.state.building }</div>
                                            </div>
                                            <Divider horizontal>Добровольная информация</Divider>
                                            <div className="profile-info-item">
                                                <div className="profile-info-item-label">Отчество</div>
                                                <div className="profile-info-item-text">{ this.state.patronymic }</div>
                                            </div>
                                            <div className="profile-info-item">
                                                <div className="profile-info-item-label">Управляющая компания</div>
                                                <div className="profile-info-item-text">{ this.state.managementCompany }</div>
                                            </div>
                                            <div className="profile-info-item">
                                                <div className="profile-info-item-label">Подьезд</div>
                                                <div className="profile-info-item-text">{ this.state.entrance }</div>
                                            </div>
                                            <div className="profile-info-item">
                                                <div className="profile-info-item-label">Этаж</div>
                                                <div className="profile-info-item-text">{ this.state.stage }</div>
                                            </div>
                                </Col>
                            </Row>   
                        </Container>
                    </Row>
                </Container>
            </Fragment>
        )
    }
}

export default Payment;