import React, { Component } from 'react'
import { Redirect } from 'react-router-dom';
import { Col, Row, Container, Table, Spinner, Button, Image, Form, Overlay, Tooltip, Popover } from 'react-bootstrap';
import './style.scss';
import { Player } from 'video-react';
import Timer from './timer';
import { withCookies } from 'react-cookie';
import { connect } from 'react-redux';
import { setUser, setAuth, removeUser } from '../../../actions';
import { bindActionCreators } from 'redux';
import VotesPeople from './votesPeople';
import { votings } from '../../../CitizenAPI';

class index extends Component {

    constructor(props) {
        super(props);
        this.attachRef = target => this.setState({ target });
        this.state = { 
            questions: [{}],
            showWarn: false,
            vote: { 
                for: 60, 
                against: 23, 
                notVoted: 25 }, 
            currentQuestion: 0, 
            effect: false, 
            watch: 0,
            comments: [
                { initials: "Иван Иванов", img: require('../../../images/avatar.png'), text: 'Прекрасно' },
                { initials: "Иван Иванов", img: require('../../../images/avatar.png'), text: 'Прекрасно' },
                { initials: "Иван Иванов", img: require('../../../images/avatar.png'), text: 'Прекрасно' },
                { initials: "Иван Иванов", img: require('../../../images/avatar.png'), text: 'Прекрасно' },
                { initials: "Иван Иванов", img: require('../../../images/avatar.png'), text: 'Прекрасно' },
                { initials: "Иван Иванов", img: require('../../../images/avatar.png'), text: 'Прекрасно' },
                { initials: "Иван Иванов", img: require('../../../images/avatar.png'), text: 'Прекрасно' },
                { initials: "Иван Иванов", img: require('../../../images/avatar.png'), text: 'Прекрасно' },
                { initials: "Иван Иванов", img: require('../../../images/avatar.png'), text: 'Прекрасно' },
                { initials: "Иван Иванов", img: require('../../../images/avatar.png'), text: 'Прекрасно' }
            ], 
            done: false, 
            showVotes: false,
            votes: [
                { initials: "Иван Иванов", vote: 'Да' },
                { initials: "Иван Иванов", vote: 'Да' },
                { initials: "Иван Иванов", vote: 'Нет' },
                { initials: "Иван Иванов", vote: 'Да' },
                { initials: "Иван Иванов", vote: 'Да' },
                { initials: "Иван Иванов", vote: 'Воздержался' },
                { initials: "Иван Иванов", vote: 'Да' },
                { initials: "Иван Иванов", vote: 'Да' },
                { initials: "Иван Иванов", vote: 'Нет' },
                { initials: "Иван Иванов", vote: 'Да' }, 
                { initials: "Иван Иванов", vote: 'Нет' },
                { initials: "Иван Иванов", vote: 'Да' }] 
            };
            this.changeShow = this.changeShow.bind(this);
    }

    componentDidMount() {
        this.setState({ questions: [
            { text: 'Принятние решение по замене труб в подвале', video: 'http://www.808.dk/vstreamer.asp?video=gizmo.mp4', completed: false, answer: null },
            { text: 'Принятние решение по замене труб в подвале 2 Принятние решение по замене труб в подвале 2', video: 'https://media.w3.org/2010/05/sintel/trailer_hd.mp4', completed: false, answer: null },
            { text: 'Принятние решение по замене труб в подвале 3', completed: false, answer: null },
            { text: 'Принятние решение по замене труб в подвале 4', completed: false, answer: null },
            { text: 'Принятние решение по замене труб в подвале 5', completed: false, answer: null },
            { text: 'Принятние решение по замене труб в подвале 6', completed: false, answer: null }]
        });

        votings({ sesseonId: this.props.cookies.get('auth') , dateFrom: new Date(Date.now() - (1000 * 60 * 60 * 24 * 7)), dateTo: new Date() }).then((data) => {
            console.log(data);
        })
    }

    doVote( arg ) {
        this.setState({ 
            effect: this.state.currentQuestion < this.state.questions.length - 1 ? true : void 0,
            currentQuestion: this.state.currentQuestion + 1 > this.state.questions.length - 1 ? 
            this.state.currentQuestion : 
            this.state.currentQuestion + 1, questions: Object.assign(this.state.questions, { [this.state.currentQuestion]: { ...this.state.questions[this.state.currentQuestion],
                completed: true, answer: arg
            }})  });
        setTimeout(() => {
            this.setState({ effect: false });
        }, 1000);    

        if(this.state.questions[this.state.questions.length - 2].completed === true) {
            this.setState({ done: true }); 
         }
        console.log(document.getElementById('quests-block').offsetWidth);
        document.getElementById('quests-block').scrollLeft = (document.getElementById('quests-block').offsetWidth / this.state.questions.length) * (this.state.currentQuestion + 1) + 10; 
    }

    hoverWatch( arg ) {
        this.setState({ watch: arg });
    }

    changeShow() {
        this.setState(prev => ({ showVotes: !prev.showVotes }));
    }

    showWarn() {
        console.log(this.state.showWarn);
        this.setState(prev => ({ showWarn: !prev.showWarn }));
    }

    render() {

        const { showWarn, target } = this.state;

        const IsLoading = () => {
             return setTimeout(() => {
                console.log(this.props.auth);
                if(this.props.auth !== true) {
                  return(
                    <Redirect to={{
                      pathname: '/',
                      state: { from: this.props.location }
                    }}/>
                  )
                }
            }); 
        }

        const Spin = () => {
            return(<div className="spinner-support"><Spinner animation="grow" variant="primary" /></div>)
        }

        return this.props.auth ? (
            <Container fluid className="votes-container">
                <Row>
                    <Col lg={3} md={12} xs={12} className="block-offset">
                        <div className="votes-table-block">
                            <h1>Голоса <span ref={this.attachRef} onClick={() => this.showWarn()}>(?)</span></h1>
                            <div className="votes-table">
                            <Table bordered hover>
                                <tbody>
                                    <tr>
                                        <td>Количество собственников (чел.)</td>
                                        <td>{ this.state.vote.for + this.state.vote.against + this.state.vote.notVoted }</td>
                                    </tr>
                                    <tr>
                                        <td>Согласны (чел.)</td>
                                        <td>{ this.state.vote.for }</td>
                                    </tr>
                                    <tr>
                                        <td>Несогласны (чел.)</td>
                                        <td>{ this.state.vote.against }</td>
                                    </tr>
                                    <tr>
                                        <td>Необходимо голосов (гол.)</td>
                                        <td>{ Math.floor((this.state.vote.for + this.state.vote.against + this.state.vote.notVoted) / 2) } </td>
                                    </tr>
                                    <tr>
                                        <td>Осталось набрать голосов до кворума (гол.)</td>
                                        <td>{ this.state.vote.notVoted }</td>
                                    </tr>
                                </tbody>
                            </Table>
                            </div>
                            <Overlay target={target} show={showWarn} placement="bottom">
                            { props => (
                                <Popover id="overlay-example" {...props}>
                                    <p className="warning"><span>*</span>Для подсчета результатов голосования количество голосов каждого собственника принято равным принадлежащей ему площади помещения</p>
                                </Popover>
                            )}
                            </Overlay>
                        </div>
                    </Col>
                    <Col lg={6} xs={12} md={12} className="votes-video block-offset">
                        <Player
                        playsInline
                        poster="/assets/poster.png"
                        src={this.state.questions[this.state.currentQuestion].video}/>
                    </Col>
                    <Col lg={3} xs={12} md={12} className="block-offset">
                            <h2>Осталось времени: <Timer seconds={86400} /></h2>
                            <div className="votes-group">
                                <div 
                                    className="votes-item" 
                                    onMouseEnter={() => this.hoverWatch(1)} 
                                    onMouseLeave={() => this.hoverWatch(0)}>
                                    <div className={this.state.watch === 1 ? "votes-watch show" : "votes-watch"}>
                                        <i className="fa fa-eye" aria-hidden="true"></i>
                                    </div>
                                    <p>Протокол</p>
                                </div>
                                <div 
                                    className="votes-item" 
                                    onMouseEnter={() => this.hoverWatch(2)} 
                                    onMouseLeave={() => this.hoverWatch(0)} 
                                    onClick={() => this.changeShow()}>
                                    <div className={this.state.watch === 2 ? "votes-watch show" : "votes-watch"}>
                                        <i className="fa fa-eye" aria-hidden="true"></i>
                                    </div>
                                    <p>Лист голосов</p>
                                </div>
                            </div>
                    </Col>
                    <VotesPeople votes={this.state.votes} show={this.state.showVotes} changeShow={this.changeShow}/>
                </Row>
                <Row className="vote-action">
                    <Col xs={6} md={6} lg={3}>

                    </Col>
                    <Col xs={12} md={12} lg={6} className="questions">
                        <div className="vote-quest">
                            {this.state.effect ? <div className="spinner"><Spin /></div> : null }
                            <div className={ this.state.effect ? "vote-title noOverflow" : "vote-title" }>
                                <p>{this.state.currentQuestion + 1}</p>
                                <span><h6>{ this.state.questions[this.state.currentQuestion].text } </h6></span>
                            </div>
                            <div className="divider"></div>
                            <div id="quests-block" className={ this.state.effect ? "quests-block noOverflow" : "quests-block" }>
                                { this.state.questions.map((item, index) => (
                                    <div key={index} className={item.completed ? "quest-item blue" : index === this.state.currentQuestion ? "quest-item active" : "quest-item"}>
                                        { item.completed ? <i className="fa fa-check" aria-hidden="true"></i> : index + 1 }
                                    </div>
                                ))
                                
                                }
                            </div>
                        </div>
                        <div>
                            <div className="vote-button">
                                { !this.state.effect ? <div className="vote green" onClick={() => this.doVote(1)} >
                                    <Button variant="success">Согласен</Button>
                                </div> 
                                : 
                                <div className="vote green">
                                    <Button variant="success">Согласен</Button>
                                </div> }
                                { !this.state.effect ? <div className="vote orange" onClick={() => this.doVote(3)}>
                                    <Button variant="warning">Воздержался</Button>
                                </div> 
                                : 
                                <div className="vote orange">
                                    <Button variant="warning">Воздержался</Button>
                                </div> }
                                { !this.state.effect ? <div className="vote red" onClick={() => this.doVote(2)}>
                                    <Button variant="danger">Не согласен</Button>
                                </div> 
                                : 
                                <div className="vote red">
                                    <Button variant="danger">Не согласен</Button>
                                </div> }
                            </div> 
                        </div>
                        <div className="comments-block">
                            <div className="comments-header">Комментарии по вопросу №{this.state.currentQuestion + 1}</div>
                            <div className="comments-textarea">
                                <p>Текст комментария</p>
                                <Form>
                                    <Form.Control as="textarea" placeholder="текст..." />
                                    <Button>Отправить</Button>
                                </Form>
                            </div>
                            <div className="comments-textzone">
                                { this.state.comments.map((item, index) => (
                                    <div key={index} className="comments-item">
                                        <div className="comments-item-header">
                                            <div className="comments-item-header-img"><Image fluid src={item.img} /></div>
                                            <div className="comments-item-header-initials">{item.initials}</div>    
                                        </div>
                                        <div className="comments-item-text">{item.text}</div>
                                    </div>
                                )) }
                            </div>
                        </div>
                    </Col>
                    <Col xs={12} md={12} lg={3}>
                    </Col>
                </Row>
            </Container>
        ) : <IsLoading />
    }
}

function mapStateToProps(state, ownProps) {
    return {
      user: state.user,
      auth: state.auth,
      cookies: ownProps.cookies
    };
}
  
const mapDispatchToProps = (dispatch) => bindActionCreators({ setUser, setAuth, removeUser }, dispatch);

export default withCookies(connect(mapStateToProps, mapDispatchToProps)(index));
