import React, { Fragment } from 'react';
import { withRouter, Redirect } from 'react-router-dom';
import { Row, Col, Container, Form, Button } from 'react-bootstrap';
import { withCookies } from 'react-cookie';
import DG from '2gis-maps';
import './style.scss';

class Role extends React.Component {
    // Tutorial: https://medium.freecodecamp.org/login-using-react-redux-redux-saga-86b26c8180e
    constructor(props) {
    super(props);
    
    this.state = {
        redirect: false,
        value: 'Выберите',
    };
    }

    componentDidMount() {
        this.map = DG.map('map', {
            'center': [56.825685, 60.61642],
            'zoom': 13
        });
        DG.marker([56.853912, 60.601246]).addTo(this.map);
    }


    render() {

        if(this.state.redirect) { //isAuthenticated) {
            return <Redirect to="/login" />;
        } else {
            return (
                <Fragment>
                    <div className="invitation-block">
                        <div className="invitation-label">
                            <span>Добавление недвижимости</span>
                        </div>
                        <p>Укажите полный адрес вашего объекта недвижимости.</p>
                        <Form>
                        <Form.Group>
                            <Form.Control placeholder="Введите адрес" onChange={(e) => this.setRole(e)} value={this.props.role || this.state.value} as="select">
                                <option disabled selected >Выберите</option>
                                <option>Дом 46</option>
                            </Form.Control>
                        </Form.Group>

                        <Form.Group>
                            <div id="map" className="map"></div>
                        </Form.Group>
                        <Form.Group>
                        <Button variant="primary" type="submit">
                            Зарегестрироваться
                        </Button>
                        </Form.Group>
                    </Form>
                    </div>
                </Fragment>
            )
        }

    }
}

export default withCookies(withRouter(Role));