import React, { Fragment } from 'react';
import { withRouter, Redirect } from 'react-router-dom';
import { Row, Col, Container } from 'react-bootstrap';
import { withCookies } from 'react-cookie';
import Form from 'react-bootstrap/Form';
import './style.scss';

class Role extends React.Component {
    // Tutorial: https://medium.freecodecamp.org/login-using-react-redux-redux-saga-86b26c8180e
    constructor(props) {
    super(props);
    this.state = {
        redirect: false,
        value: '',
    };
    this.setRole = this.setRole.bind(this);
    }

    setRole(e) {
        this.props.setRole(e.target.value);
    }

    render() {

        if(this.state.redirect) { //isAuthenticated) {
            return <Redirect to="/login" />;
        }
        else {
            return (
                <Fragment>
                    <Container className="register-container">
                        <Row>
                            <Col className="register-block">
                                <Form>
                                    <Form.Group>
                                        <Form.Label>Выберите вашу роль</Form.Label>
                                        <Form.Control onChange={(e) => this.setRole(e)} value={this.props.role === '' ? 'Выберите' : this.props.role} as="select">
                                            <option selected={true} disabled>Выберите</option>
                                            <option>Сожитель</option>
                                            <option>Собственник</option>
                                            <option>Арендатор</option>
                                            <option>ТСЖ</option>
                                            <option>Председатель совета</option>
                                            <option>УК</option>
                                        </Form.Control>
                                    </Form.Group>
                                </Form>
                            </Col>
                        </Row>
                    </Container>
                </Fragment>
            )
        }

    }
}

export default withCookies(withRouter(Role));